import React from "react";

const Invited = (props) => {
  const sendData = () => {
    props.click(props.data);
  };
  return (
    <div className=" w-75 col-lg-4 col-sm-6 mt-4" style={{ margin: "auto" }}>
      <div className="card text-center shadow-sm p-3 mb-3 bg-white rounded">
        <div className="card-body">
          <div className="card-title">
            <h5>{props.data.firstName}</h5>
          </div>
          <p className="card-text">{props.data.email}</p>
          <button className="btn btn-primary" onClick={sendData}>
            Responder a invitacion
          </button>
        </div>
      </div>
    </div>
  );
};

export default Invited;
