import React from "react";
import "../App.css";
import Invited from "./Invited";
import { Component } from "react";
import CustomizedDialogs from "./DialogBox";
import { Divider } from "@material-ui/core";
import swal from "sweetalert";
import Navbar from "../Navbar";
const { GoogleSpreadsheet } = require("google-spreadsheet");
const creds = require("../client_secret.json");

const styles = {
  searchBar: {
    maxWidth: "300px",
  },
};

const doc = new GoogleSpreadsheet(
  "11tH_WVs1vmYYrm0EbE8Yx6FUqyBjl-nxF2e6Ag7Vtgo"
);

let rows;

export class RSVP_Page extends Component {
  state = {
    count: 0,
    data: [],
    search: "",
    clickedData: {},
    familyMembers: [],
    successSubmit: true,
  };

  componentDidMount = async () => {
    await doc.useServiceAccountAuth({
      client_email: creds.client_email,
      private_key: creds.private_key,
    });

    await doc.loadInfo();
    const sheet = doc.sheetsByIndex[0];
    rows = await sheet.getRows();

    this.setState({ data: rows });
  };

  clickedButton = async () => {
    rows[0].PlusOne = "TE AMO HERMOSOTA";
    await rows[0].save();
    this.setState({ data: rows });

    console.log("hey");
  };

  updateSearch(event) {
    this.setState({ search: event.target.value });
  }

  getClickInfo = (data) => {
    this.setState({
      clickedData: data,
    });

    let familyFiltered = this.state.data.filter((row) => {
      if (row.index !== data.index) {
        return row.groupId.indexOf(data.groupId) !== -1;
      }
    });

    this.setState({ familyFiltered: familyFiltered });
  };

  closeDialog = () => {
    this.setState({ clickedData: {} });
  };

  submitted = () => {
    swal("Su respuesta ha sido recibida!", "Gracias!", "success");
  };

  render() {
    let dataFiltered = this.state.data.filter((row) => {
      let name = row.firstName.toLowerCase() + " " + row.lastName.toLowerCase();
      let nameReverse =
        row.lastName.toLowerCase() + " " + row.firstName.toLowerCase();
      return (
        (name.toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1) +
        (nameReverse.toLowerCase().indexOf(this.state.search.toLowerCase()) !==
          -1)
      );
    });

    return (
      <div>
        <div>
          <input
            className="form-control mt-3 mb-2 ml-3 "
            type="text"
            placeholder="Buscar"
            aria-label="Search"
            style={styles.searchBar}
            onChange={this.updateSearch.bind(this)}
          ></input>
        </div>
        <Divider className="shadow-sm p-1 mb-3 bg-white rounded" />
        <div className="row">
          <div className="wrapper container">
            <div className="card-deck">
              {dataFiltered.map((item) => (
                <Invited
                  key={item.index}
                  data={{
                    index: item.index,
                    groupId: item.groupId,
                    firstName: item.firstName,
                    lastName: item.lastName,
                    email: item.email,
                    going: item.going,
                    plusOne: item.plusOne,
                    confirmedPlusOne: item.confirmedPlusOne,
                  }}
                  click={this.getClickInfo.bind(this)}
                />
              ))}
            </div>
            {Object.keys(this.state.clickedData).length !== 0 && (
              <CustomizedDialogs
                data={this.state.clickedData}
                familyData={this.state.familyFiltered}
                closed={this.closeDialog.bind(this)}
                submit={this.submitted.bind(this)}
                sheetRows={rows}
              />
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default RSVP_Page;
