import React from "react";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormLabel from "@material-ui/core/FormLabel";

const InviteResponseSelect = (props) => {
  const [value, setValue] = React.useState("");

  const handleChange = (event) => {
    setValue(event.target.value);
    sendData(event.target.value);
  };

  const sendData = (value) => {
    if (props.data.hasPlus !== false) {
      props.sendResponse({ index: "plusOne", response: value });
    } else {
      props.sendResponse({ index: props.data.index, response: value });
    }
  };

  return (
    <div style={{ display: "flex" }}>
      <div
        style={{
          width: "50%",
          float: "left",
          margin: "auto",
          textAlign: "center",
        }}
      >
        <p style={{ marginTop: "6px", fontWeight: "500", fontSize: "16px" }}>
          {props.name}:
        </p>
      </div>
      <div
        style={{
          width: "50%",
          float: "left",
          margin: "auto",
          textAlign: "center",
        }}
      >
        <FormControl component="fieldset">
          <RadioGroup
            aria-label="gender"
            name="gender1"
            value={value}
            onChange={handleChange}
          >
            <FormControlLabel
              value="going"
              control={<Radio color="primary" />}
              label="Asistira"
            />
            <FormControlLabel
              value="not sure yet"
              control={<Radio color="primary" />}
              label="Todavia no sabe"
            />
            <FormControlLabel
              value="not going"
              control={<Radio color="primary" />}
              label="No asistira"
            />
          </RadioGroup>
        </FormControl>
      </div>
    </div>
  );
};

export default InviteResponseSelect;
